.timetable{
    background-color: $mainColor;
    @media screen and (min-width:$desktop){
        padding-top: 121px;
        padding-bottom: 121px;
    }
}
.timetable__title{
    color: $aboutTextColor;
    font-weight: 500;
    text-align: center;
    @media screen and (min-width:$desktop){
        font-size: 35px;
        line-height: 1;
        letter-spacing: 0.02em;
        margin-bottom: 40px;
    }
}