.sign-up{
    @media screen and (min-width:$desktop){
        background-image: url(../images/hero/hero-desk.jpg);
        background-position: center;
        background-size: cover;
        padding-top: 123px;
        padding-bottom: 475px;
        @media (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
                background-image: url(../images/hero/hero-desk@2x.jpg);
            }
    }
}

.form__title{
    @media screen and (min-width:$desktop){
        font-size: 35px;
        line-height: 1;
        letter-spacing: 0.02em;
        font-weight: 500;
        margin-bottom: 54px;
    }
}
.form__item{
    @media screen and (min-width:$desktop){
        &:not(:last-child){
            margin-bottom: 18px;
        }
    }
}
.form__label{
    position: relative;
}
.form__input{
    padding: 8px 0 8px 34px;
    border: 3px solid #F6D88A;
    border-radius: 20px;
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    &:focus{
        box-shadow:0px 0px 25px #FEE690;
    }
    @media screen and (min-width:$desktop){
        font-size: 30px;
        line-height: 1.17;
        letter-spacing: 0.02em;
        padding: 12px 0 12px 57px;
        
        &::placeholder{
            font-size: 30px;
                line-height: 1.17;
                letter-spacing: 0.02em;
                color: $mainColor;
        }
    }
}
.form__icons{
    position: absolute;
    @media screen and (min-width:$desktop){
        top: -10px;
        left: 10px;
    }
}